.wrapper {
  position: relative;
  display: flex;
  flex-flow: column;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  padding: 0;
}

.room__link {
  margin: 0.5rem;
  font-size: x-small;
}

.container {
  position: relative;
  width: 100%;
  height: 90vh;
  display: grid;
  grid-gap: 0;
  overflow: hidden;
}

.video {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 0;
  min-width: 0;
  transition: width 0.3s ease-in;
}
.video video {
  cursor: pointer;
}

.video__fixed {
  z-index: 12;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.95);
}
.video__fixed video {
  cursor: default;
}

.actions {
  position: relative;
  display: flex;
  height: 10vh;
  width: 100%;
  background-image: rgba(128, 128, 128, 0.05);
}